import uniSwipeAction from '@/components/uni-swipe-action/uni-swipe-action.vue';
import { getMessageList, readMessage, deleteMessage } from '@/api';
export default {
	data() {
		return {
			message: [],
			options2: [
				{
					text: '删除',
					style: {
						backgroundColor: 'rgb(255,58,49)'
					}
				}
			]
		};
	},
	onLoad() {},
	onShow() {
		this._getMessageList();
	},
	onPullDownRefresh(e) {
		this._getMessageList();
	},
	methods: {
		async _getMessageList() {
			// 获取消息列表
			uni.showLoading({
				title: '正在加载数据...'
			});
			let result = await getMessageList({
				token: uni.getStorageSync('token')
			});
			uni.hideLoading();
			uni.stopPullDownRefresh();
			this.message = result.data || [];
		},
		async delMessage(item) {
			let self = this;
			// 删除消息
			uni.showModal({
				content: '确定要删除此条消息吗？',
				success: function (res) {
					if(res.confirm) {
						deleteMessage({
							token: uni.getStorageSync('token'),
							me_id: item.id
						}).then(result => {
							if (result.recode == 10000) {
								self._getMessageList();
								uni.showToast({
									title: '消息删除成功'
								});
							} else {
								uni.shoToast({
									title: result.remsg
								});
							}
						});	
					}
				}
			})
			
		},
		async switchTo(item, index) {
			// 跳转消息详情并改变消息阅读状态
			let result = await readMessage({
				token: uni.getStorageSync('token'),
				me_id: item.id,
				type: item.type
			});
			uni.navigateTo({
				url: `/pages/mine/subPages/messageDetail/messageDetail?title=${item.title}&id=${item.id}`,
				animationType: 'slide-in-right'
			});
		}
	},
	components: {
		uniSwipeAction
	}
};
